import React from 'react';
import ReactDOM from 'react-dom';

//import { __ } from '@wordpress/i18n';

import Fetcher from '../../js/utils/fetcher';
//import Clipboard from '../../js/utils/clipboard';

import Notice from '../../components/notice'
import GzipTableTwoColumns from '../../components/gzip-table-two-columns';
import GzipServerInstructions from "../../components/gzip-server-instructions";

class Gzip extends React.Component {
	/**
	 * Component constructor.
	 *
	 * @param props
	 */
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			autoEnabled: false,
			currentServer: '',
			externalProblem: false,
			inactiveTypes: 3,
			gzipStatus: [{
				type: 'HTML',
				accessor: 'html',
				status: false
			}, {
				type: 'JavaScript',
				accessor: 'js',
				status: true
			}, {
				type: 'CSS',
				accessor: 'css',
				status: false
			}],
			serverList: [],
		};
	}

	/**
	 * Init component states.
	 */
	componentDidMount() {
		Fetcher.gzip.initValues()
			.then(( response ) => {
				if ( 'undefined' !== typeof response ) {
					let gzipStatus = this.state.gzipStatus;
					gzipStatus.map(( item ) => {
						item.status = response.status[ item.type ];
					});

					this.setState({
						loading: false,
						/** @param {string} response.server */
						currentServer: response.server,
						/** @param {Object} response.servers */
						serverList: Object.entries( response.servers ),
						gzipStatus: gzipStatus,
						inactiveTypes: gzipStatus.filter( item => ! item.status ).length
					});
				}
			});

		/**
		 * Get current server
		 */
		/*
		Fetcher.gzip.getCurrentServer()
			.then(( response ) => {
				if ( 'undefined' !== typeof response && response.server ) {
					this.setState({ currentServer: response.server });
				}
			});
		*/

		/**
		 * Get a list of servers.
		 */
		/*
		Fetcher.gzip.getServerList()
			.then(( response ) => {
				if ( 'undefined' !== typeof response && response.servers ) {
					this.setState({ serverList: response.servers });
				}
			});
		*/

		/**
		 * Get gzip status
		 */
		/*
		Fetcher.gzip.getStatus()
			.then(( response ) => {
				if ( 'undefined' !== typeof response && response.status ) {
					let gzipStatus = this.state.gzipStatus;

					gzipStatus.map(( item ) => {
						item.status = response.status[ item.type ];
					});

					this.setState({
						gzipStatus: gzipStatus,
						inactiveTypes: gzipStatus.filter( item => ! item.status ).length
					});
				}
			});
		*/
	}

	/**
	 * Share UI actions need to be performed manually for elements.
	 * They should be done in this method.
	 */
	componentDidUpdate() {
		const el = document.getElementById( 'wphb-server-type' );
		suiSelect( el );
	}

	/**
	 * Show notice.
	 *
	 * @param {string} type  Notice type.
	 * @returns {React.Component}
	 */
	showNotice(type = 'status') {
		let noticeType = 'success';
		let noticeText = <p>GZip compression is currently active. Good job!</p>;

		if ( 'externalProblem' === type && this.state.externalProblem ) {
			noticeType = 'error';
			noticeText = ( // TODO: span needs to be removed
				<span>
					<p>Gzip is not working properly:</p>
					<ul>
						<li>- Your server may not have the "deflate" module enabled (mod_deflate for Apache,
							ngx_http_gzip_module for NGINX).</li>
						<li>- Contact your host. If deflate is enabled, ask why all .htaccess or nginx.conf
							compression rules are not being applied.</li>
					</ul>
					<p>If re-checking and restarting does not resolve, please check with your host or
						<a href="%s" target="_blank">open a support ticket with us</a>.</p>
				</span>
			);

			return <Notice type={ noticeType } text={ noticeText } />
		}

		if ( 'status' === type ) {
			if ( 0 < this.state.inactiveTypes ) {
				noticeType = 'warning';
				noticeText = <p>{ this.state.inactiveTypes } of your compression types are inactive.
					<a href="#" id="configure-gzip-link">Configure</a> compression for all files types below.</p>;
			}

			return <Notice type={ noticeType } text={ noticeText } />
		}
	}

	/**
	 * Render summary meta box.
	 *
	 * @returns {React.Component}
	 */
	renderSummaryMetabox() {
		const columns = [{
			header: 'File type',
			accessor: 'type'
		}, {
			header: 'Current status',
			accessor: 'status'
		}];

		return (
			<div id="wphb-box-gzip-summary" className="sui-box box-gzip-summary">
				<div className="sui-box-header">
					<h3 className="sui-box-title">Summary</h3>
					<div className="sui-actions-right">
						<button className="sui-button sui-button-ghost" onClick={ () => this.updateStatus() }>
							Re-Check Status
						</button>
					</div>
				</div>
				<div className="sui-box-body">
					{ this.showNotice('externalProblem') }

					<p>Gzip compresses your web pages and style sheets before sending them over to the browser. This
						drastically reduces transfer time since the files are much smaller.</p>

					{ this.showNotice() }

					<GzipTableTwoColumns columns={ columns } data={ this.state.gzipStatus } />
				</div>
			</div>
		);
	}

	renderServerConfig() {
		if ( this.state.autoEnabled ) {
			return (
				<div className="sui-box-settings-col-2">
					<Notice type="info" text="Automatic .htaccess rules have been applied." />
					<a href="#" className="sui-button sui-button-ghost">Deactivate</a>
				</div>
			);
		}

		return <GzipServerInstructions
			currentServer={ this.state.currentServer }
			htaccessWritable="true"
			htaccessWritten="true"
			htaccessError="false"
			showEnableButton="true"
		/>;
	}

	/**
	 * Render settings meta box.
	 *
	 * @returns {React.Component}
	 */
	renderSettingsMetabox() {
		const serverOptions = this.state.serverList.map(( server ) => {
			return <option value={ server[0] }>{ server[1] }</option>
		});

		return (
			<div id="wphb-box-gzip-settings" className="sui-box box-gzip-settings">
				<div className="sui-box-header">
					<h3 className="sui-box-title">Configure</h3>
				</div>
				<div className="sui-box-body">
					<div className="sui-box-settings-row">
						<div className="sui-box-settings-col-1">
							<span className="sui-settings-label">Server type</span>
							<span className="sui-description">
								Choose your server type. If you dont know this, please contact your hosting provider.
							</span>
						</div>
						<div className="sui-box-settings-col-2">
							<label htmlFor="wphb-server-type" className="inline-label">Server type</label>
							<select name="wphb-server-type" id="wphb-server-type">
								{ serverOptions }
							</select>
						</div>
					</div>

					<div className="sui-box-settings-row">
						<div className="sui-box-settings-col-1">
							<span className="sui-settings-label">Enable compression</span>
							<span className="sui-description">
								Follow the instructions to activate GZip compression for this website.
							</span>
						</div>
						{ this.renderServerConfig() }
					</div>
				</div>
			</div>
		);
	}

	/**
	 * Render component.
	 *
	 * @returns {*}
	 */
	render() {
		if ( this.state.loading ) {
			return <div>Loading...</div>
		}

		return (
			<div>
				{ this.renderSummaryMetabox() }
				{ this.renderSettingsMetabox() }
			</div>
		);
	}
}

document.addEventListener('DOMContentLoaded', function() {
	ReactDOM.render(
		<Gzip />,
		document.getElementById('wrap-wphb-react-gzip')
	);
});